<template>
  <canvas ref="chart"></canvas>
</template>

<script>
import Chart from "chart.js";

export default {
  name: "Chart",
  data() {
    return {};
  },
  props: [
    "chartData",
    "chartAxisLabels",
    "chartLabel",
    "borderColor",
    "backgroundColor"
  ],
  watch: {
    chartData: {
      deep: true,
      handler() {
        this.buildLineGraph();
      }
    },
    chartAxisLabels: {
      deep: true,
      handler() {
        this.buildLineGraph();
      }
    }
  },
  methods: {
    buildLineGraph() {
      var ctx = this.$refs.chart.getContext("2d");
      new Chart(ctx, {
        type: "line",
        data: {
          labels: this.chartAxisLabels,
          datasets: [
            {
              label: this.chartLabel,
              data: this.chartData,
              backgroundColor: this.backgroundColor,
              borderColor: this.borderColor,
              borderWidth: 1
            }
          ]
        },
        options: {
          scales: {
            yAxes: [
              {
                ticks: {
                  beginAtZero: true
                }
              }
            ]
          }
        }
      });
    }
  },
  mounted() {
    this.buildLineGraph();
  }
};
</script>
